.container {
  margin-top: 10rem;
  max-width: 100%;
  margin: 0 auto;
  background-color: #005051;
  min-height: 80vh;
  padding: 0;
  box-sizing: border-box;
}
.container .searchBar {
  padding-top: 5rem;
}
.container .title {
  font-size: 30px;
  padding: 1rem 0rem 0.5rem 0rem;
}
.container .detail {
  font-size: 22px;
  margin-bottom: 1rem;
}

.callUs {
  padding: 5rem 1rem 5rem 3rem;
  background-color: rgba(0, 0, 0, 0.568);
  color: #fff;
}

.textContainer {
  display: flex;
  width: 100%;
  justify-content: space-between;
  background-color: #005051;
  color: #fff;
  margin-top: 2rem;
}

@media screen and (max-width: 1025px) {
  .container .title {
    font-size: 25px;
    padding: 1rem 0rem 0.5rem 0rem;
  }
}
@media screen and (max-width: 768px) {
  .textContainer {
    display: flex;
    flex-direction: column;
    background-color: #005051;
    color: #fff;
  }
  .callUs {
    padding-bottom: 4rem;
    padding-left: 3rem;
    background-color: rgba(0, 0, 0, 0.568);
    color: #fff;
  }
}/*# sourceMappingURL=Contact.module.css.map */